<template>
  <div class="row q-col-gutter-md">
    <div
      class="col-12 col-sm-6"
      v-for="(platform, i) in platforms"
      :key="platform"
    >
      <q-radio
        :dense="dense"
        autofocus
        hide-bottom-space
        v-model="modelValue"
        :val="platform"
        color="secondary"
        :name="name || undefined"
        class="field-select bg-none text-body1 font-medium text-primary-500 rounded q-py-sm q-pl-sm full-width flex-label"
        :rules="[(val) => (required ? !!val || '' : true)]"
      >
        <q-icon
          :name="formatPlatformIconSrc(platform)"
          size="xs"
          class="q-pr-sm"
        />
        {{ getPlatformInfo(platform, 'title') }}
        <q-badge
          rounded
          v-if="i === 0"
          class="text-secondary bg-accent text-caption font-medium q-px-sm q-ml-sm letter-space-normal"
        >
          Recommended
        </q-badge>
      </q-radio>
    </div>
    <div class="col-12 col-sm-6">
      <q-radio
        :dense="dense"
        hide-bottom-space
        v-model="modelValue"
        val="no-preference"
        label="No Preference"
        color="secondary"
        :name="name || undefined"
        class="field-select bg-none text-body1 font-medium text-primary-500 rounded q-py-sm q-pl-sm full-width"
        :rules="[(val) => (required ? !!val || '' : true)]"
      />
    </div>
  </div>
</template>

<script>
import { defineComponent } from 'vue';
import { mapActions } from 'pinia';
import useGrowmodo from 'stores/growmodo';

export default defineComponent({
  name: 'SelectPlatforms',

  emits: ['updated'],

  props: {
    initModel: {},
    platforms: {
      type: Array,
      default: () => [],
    },
    name: {
      type: String,
      default: '',
    },
    required: {
      type: Boolean,
      default: false,
    },
    dense: {
      type: Boolean,
      default: true,
    },
  },

  data() {
    return {
      modelValue: '',
    };
  },

  watch: {
    initModel: {
      async handler(val) {
        if (val) this.modelValue = val;
      },
    },
    modelValue: {
      async handler(val) {
        this.$emit('updated', val);
      },
    },
  },

  mounted() {
    if (this.initModel) this.modelValue = this.initModel;
  },

  methods: {
    ...mapActions(useGrowmodo, ['getPlatformInfo', 'formatPlatformIconSrc']),
  },
});
</script>
